@import url('https://fonts.googleapis.com/css2?family=VT323&display=swap');

:root {
  --retro-green: #00ff00;
  --retro-green-dark: #00cc00;
  --retro-bg: #000000;
  --retro-text: #dcdcdc;
  --retro-glow: rgba(0, 255, 0, 0.5);
  --retro-glow-light: rgba(0, 255, 0, 0.3);
  --retro-glow-dark:  #1d1f24;
}

html {
  scroll-behavior: smooth; /* enables smooth scroll for hash/ScrollTo */
}


body {
  margin: 0;
  background-color: black;
  color: var(--retro-text);
  font-family: 'VT323';
  overflow-x: hidden;
  line-height: 1.6;
}

.App {
  display: flex;
  flex-direction: column;
  overflow: visible;
}

.page {
  display: flex;
  flex-direction: row;
  position: relative;
  background: black;
}

.main {
  /* Full screen area (adjust if header stays fixed) */
  display: flex;
  /* Center content and keep your theme vars */
  background: transparent;
  color: var(--retro-text);

  --panel-bg: var(--retro-bg);
  margin-bottom: 15vh;
  /* Optional: if you want some breathing room around the whole thing */
  /* padding: 1.5rem 2vw; */
}

.main-panel {
  flex: 1;                   
  overflow: none;  
  background: transparent;
  color: var(--retro-text);
  flex-direction: column;
  display: flex;
  justify-content: center;
  margin-bottom: 5vh;
}
.content-section{
  display: flex;
  justify-content: center;
  border-top: 1px dashed rgba(128,128,128,.35); /* faint grey */
  padding-top: 12px; 
  height:100vh;/* keeps text off the border */
}

/* Constrain the content width without changing your component */
.main-panel > * {
  width: 100%;        /* match your previous container */
}

/* Smooth swap animation you already had */
.swap-in {
  animation: swapIn 240ms ease-out;
}
@keyframes swapIn {
  from { opacity: 0; transform: translateY(8px); }
  to   { opacity: 1; transform: translateY(0); }
}


@media (max-width: 720px) {
  .main{
    margin-bottom: 0;
  }
}

@media (min-width: 721px){
  .index-0{
    height:30vh;
  }
  .index-1{
    height:55vh;
  }
  .index-2{
    height:50vh;
  }
  .index-3{
    height:55vh;
  }
}